import React, { useState } from "react";

export const AppContext = React.createContext();

export const AppState = ({ children }) => {
  const [chargeabilityForecast, setChargeabilityForecast] = useState([]);

  return (
    <AppContext.Provider
      value={{ chargeabilityForecast, setChargeabilityForecast }}
    >
      {children}
    </AppContext.Provider>
  );
};
