import React, { useContext } from "react";
import { AppContext } from "./AppState";

const AverageReport = () => {
  const { chargeabilityForecast, setChargeabilityForecast } = useContext(
    AppContext
  );

  if (chargeabilityForecast.length == 0) {
    return <div>No data</div>;
  }

  const allNumOnes = chargeabilityForecast.map(row => row.num1);
  const sum = allNumOnes.reduce((previous, current) => previous + current);
  const avg = sum / allNumOnes.length;

  return (
    <section>
      <h1>Average Report</h1>
      <h2>Average: {avg}</h2>
    </section>
  );
};

export default AverageReport;
